.toggle {
    height: 20px;
    width: 40px;
    border-radius: 10px;
    margin: auto;
    background-image: linear-gradient(aqua, skyblue);
    position: relative;
    cursor: pointer;
}

.toggle.night {
    background-image: linear-gradient(midnightblue, rebeccapurple);
}

.notch {
    top: 1px;
    left: 1px;
    height: 18px;
    width: 18px;
    position: absolute;
    z-index: 1;
    border-radius: 50%;
    background: yellow;
    box-shadow: 0 0 1px yellow;
    transition: all 0.3s ease;

    & > .crater {
        background: burlywood;
        border-radius: 50%;
        position: absolute;
        opacity: 0;
        box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4) inset;
    }
}

.night .crater {
    opacity: 0.4;
}

.crater:first-child {
    left: 1px;
    top: 3px;
    height: 3px;
    width: 8px;
    transform: rotate(-45deg);
}

.crater:last-child {
    right: 2px;
    top: 3px;
    height: 3px;
    width: 5px;
    transform: rotate(45deg);
}

.night > .notch {
    background: whitesmoke;
    box-shadow: 0 0 1px whitesmoke;
    transform: translate(20px, 0);
}

.shape {
    position: absolute;
    background: whitesmoke;
    border-radius: 50%;
    transition: all 0.3s ease;

    &.sm {
        height: 1px;
        width: 10px;
        top: 50%;
        left: 60%;
    }

    &.md {
        height: 2px;
        width: 15px;
        top: 25%;
        left: 25%;
        z-index: 2;
    }

    &.lg {
        height: 3px;
        width: 20px;
        bottom: 3px;
        left: 25%;
    }
}

.night .shape {
    background: lightgray;
    box-shadow: 0 0 2px 0.5px violet;
}

.night .shape.sm {
    height: 1px;
    width: 1px;
    transform: translate(-5px, 0);
}

.night .shape.sm:first-of-type {
    transform: translate(-10px, -1px);
}

.night .shape.md {
    height: 2px;
    width: 2px;
    transform: translate(1px, 0);
}

.night .shape.lg {
    height: 3px;
    width: 3px;
    transform: translate(-1px, 0);
}
